body.light {
  background-color: #ffffff;
  color: #000000;
  transition: all 0.5s ease-in-out;
}

body.dark {
  background-color: #000000;
  color: #ffffff;
  transition: all 0.5s ease-in-out;
}

body {
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 100vh;
  align-items: center;
}

button{
  padding: 1rem 1.5rem;
  font-size: 1.4rem;
  border-radius: 1rem;
  background-color: red;
  color: white;
  border: none;
}
